.trainer-block {
  background-color: $white;
}

.trainer-block__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 1160px;
  padding-top: 0;
  padding-bottom: 120px;

  @media (max-width: $tablet-width) {
    padding-bottom: 100px;
  }

  @media (max-width: $mobile-width) {
    padding-bottom: 70px;
  }
}

.trainer-block__wrapper h2 {
  @include default-style;
  @include headtitle-desktop;

  padding-bottom: 50px;

  text-align: left;

  @media (max-width: $tablet-width) {
    @include headtitle-tablet;

    padding-bottom: 43px;
    text-align: center;
  }

  @media (max-width: $mobile-width) {
    @include headtitle-mobile;

    padding-bottom: 42px;
  }
}

.trainer-block__info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  width: 100%;

  margin: auto;

  @media (max-width: $tablet-width) {
    justify-content: center;
    width: 100%;
  }
}

.trainer-block__list {
  position: relative;

  @include default-style;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  width: 100%;

  &::before {
    content: "";
    position: absolute;

    top: 45%;
    left: -3%;

    display: block;

    width: 30px;
    height: 30px;

    border: solid #afafaf;
    border-width: 0 1px 1px 0;


    border-bottom: 1px solid #afafaf;
    border-right: 1px solid #afafaf;

    transform: rotate(-45deg);
    transform: matrix(-0.55, -0.52, -0.52, 0.55, 0, 0);
  }

  &::after {
    content: "";
    position: absolute;

    top: 45%;
    right: -3%;

    display: block;

    width: 30px;
    height: 30px;

    border-top: 1px solid #afafaf;
    border-left: 1px solid #afafaf;

    transform: rotate(-45deg);
    transform: matrix(-0.55, -0.52, -0.52, 0.55, 0, 0);
  }

  @media (max-width: $tablet-width) {
    justify-content: center;

    width: 83.48%;

    &::before {
      top: 45%;
      left: -55px;
    }

    &::after {
      top: 45%;
      right: -55px;
    }
  }

  @media (max-width: $mobile-width) {
    min-width: 226px;
    width: 77.93%;

    margin: 0 auto;

    &::before {
      top: 47%;
      left: -30px;

      width: 16px;
      height: 16px;
    }

    &::after {
      top: 47%;
      right: -30px;

      width: 16px;
      height: 16px;
    }

  }
}

.trainer-block__item h3 {
  @include default-style;

  position: absolute;
  z-index: 1;

  left: 50%;
  top: 245px;
  transform: translateX(-50%);

  font-family: $font-roboto;
  font-weight: bold;
  font-size: 18px;
  line-height: 150%;

  color: $white;
  white-space: nowrap;

  text-align: center;

  @media (max-width: $mobile-width) {
    top: 220px;
  }
}

.trainer-block__item {
  position: relative;

  width: 260px;
  min-height: 294px;
  height: 100%;

  &:hover,
  &:focus {
    h3 {
      z-index: 5;
      top: 53px;
    }
  }

  &:hover,
  &:focus {
    .trainer-block__cover {
      opacity: 0.89;
    }
  }

  @media (max-width: $tablet-width) {
    width: 268px;
    margin-right: 30px;

    &:hover,
    &:focus {
      h3 {
        top: 50px;
      }
    }
  }

  @media (max-width: $mobile-width) {
    width: 226px;
    min-height: 274px;
    margin-right: 0;

    &:hover,
    &:focus {
      h3 {
        top: 42px;
      }
    }
  }
}

.trainer-block__cover {
  position: absolute;

  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  box-sizing: border-box;

  padding-top: 76px;
  padding-bottom: 20px;

  background-color: $darkblue;

  opacity: 0;


  @media (max-width: $tablet-width) {
    padding-top: 75px;
    padding-bottom: 58px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 65px;
    padding-bottom: 48px;
  }
}

.trainer-block__cover p {
  @include default-style;

  margin-bottom: 33px;

  font-family: $font-roboto;
  font-size: 16px;
  line-height: 150%;

  color: #b6bdcc;

  text-align: center;

  @media (max-width: $tablet-width) {
    margin-bottom: 33px;
  }
}

.trainer-block__cover-list {
  @include default-style;

  padding-left: 55px;

  @media (max-width: $mobile-width) {
    padding-left: 35px;
  }
}

.trainer-block__cover-item {
  @include default-style;

  position: relative;

  margin-bottom: 14px;
  padding-right: 28px;
  padding-left: 16px;

  font-family: $font-roboto;
  font-size: 14px;
  line-height: 150%;

  color: $white;

  text-align: left;

  &:last-child {
    margin-bottom: 0;
  }

  &::before {
    content: "";
    position: absolute;

    top: 6px;
    left: 0;
    display: block;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: $white;
  }
}

.trainer-block__item--block {
  @media (max-width: $tablet-width) {
    display: none;
  }

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.trainer-block__item--mob {
  @media (max-width: $mobile-width) {
    display: none;
  }
}

.trainer-block__img {
  display: block;

  @media (max-width: $tablet-width) {
    width: 268px;
    height: 294px;
  }

  @media (max-width: $mobile-width) {
    width: 226px;
    height: 274px;
  }
}


.trainer-block__img img{
  @media (max-width: $tablet-width) {
    width: 100%;
    height: 100%;

    overflow: hidden;
  }
}
